.main {
  width: 100%;
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  & a {
    text-decoration: none !important;
  }

  .chatWindow {
    background: #ebecf3;
    box-shadow: 0 0 5px #95A5A6;
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
    height: 60%;
    width: 600px;
    overflow-y: scroll;


    @media (max-width: 900px) {
      width: 80%;

    }
  }

  .messageField {
    width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px 0;
    padding: 10px;

    @media (max-width: 900px) {
      width: 80%;

    }
  }

  .messageButton {
    display: flex;
    justify-content: center;
    margin: 10px 0;
    padding: 10px;
  }

  .header {
    display: flex;
    justify-content: center;
  }


}







